.menu-bg {
  position: absolute;
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  background: rgba(167, 167, 167, 0.268);
  z-index: 4;
}

.container {
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;

  .company-logo {
    position: relative;
    height: 50px;

    img {
      position: absolute;
      top: 20px;
      left: 20px;
    }
  }

  .header {
    height: 100px;
    display: flex;
    align-items: center;
    padding: 0 15px;
    font-size: 14px;
    font-weight: 500;
    color: black;
    background-color: #ffff;

    button {
      border-radius: 5px;
      font-weight: 500;
    }

    .header-line {
      max-width: 1200px;
      width: 100%;
      display: flex;
      justify-content: space-between;

      @media (max-width: 1228px) {
        display: block;
        margin-top: 30px;
      }

      @media (max-width: 690px) {
        display: none;
      }

      button {
        a {
          color: black;
        }
        .selected {
          color: rgb(0, 153, 255);
        }

        @media (max-width: 1228px) {
          margin: 0px 10px 10px 10px;
        }
      }
    }

    .media-menu {
      display: none;

      .menu-btn {
        left: 190px;
        max-width: 100px;
        width: 100%;
      }

      .active-btn {
        color: rgb(0, 153, 255);
        background: white;
      }

      .disabled-btn {
        color: white;
        background: rgb(0, 153, 255);
      }

      @media (max-width: 690px) {
        z-index: 5;
        display: block;
        position: absolute;
        top: 25px;
        right: 10px;
      }
    }
  }
}

.ant-dropdown-menu-item > a {
  color: black;
}

.ant-dropdown-menu-item > a:hover {
  color: rgb(0, 153, 255);
}
